@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
*{
	margin: 0;
	padding: 0;
	outline: 0;
}
body{
	background-color:#ecf0f1;
}

body, input, button, textarea{
	font-size:1em;
	font-family: 'Montserrat', sans-serif;
	color:#2c3e50;
}
header{
	width:100%;
	padding: 15px 0px;
}
header h2{
	margin: 2px 15px;
}
.clear{
	clear: both;
}
.float-right{
	float: right;
}
.float-left{
	float: left;
}

.btn{
	border:0;
	padding: 10px 18px;
	background-color:#bdc3c7;
	margin:5px 5px;
	border-radius:5px;
	-webkit-transition: all .7s;
	transition: all .7s;
}

.form-group{
	display: flex;
	width:98%;
	padding: 15px 1%;
	flex-direction: column;
}
.form-group > .form-control{
	border:0;
	width:90%;
	margin:0 auto;
	padding: 10px 8px;
	border-radius: 5px;
	background-color:#f1f1f1;
	border:1px solid #2c3e50;
}
.form-group > label{
	color:#2c3e50;
	font-weight: bold;
}


.btn:hover{
	background-color:#95a5a6;
}
.btn-primary{
	background-color:#3498db;
	color:#ecf0f1;
}
.btn-primary:hover{
	background-color:#2980b9;
}

footer{
	position: relative;
	bottom: 0;
	padding: 15px 0px;
	width:100%;
}
footer p{
	color:#2c3e50;
	font-weight: bold;
	text-align: center;
}
/* FORMULARIO */
.formulario{
	max-width:50%;
	margin: 0 auto;
	background-color:#27ae60;
	padding: 50px 15px;
	border-radius:5px;
	margin:5% auto;
}

.clear{clear:both;}
.btn-danger{
    position: absolute;
    top:15px;
    right: 15px;
    border:0;
    background-color:#e74c3c;
    color:#FFF;
    padding: 10px 20px;
    -webkit-transition: .8s;
    transition: .8s;
    font-weight: bold;
}
.btn-danger:hover{
    background-color: #c0392b;
}

.item{
    padding: 15px;
    background-color:#FFF;
    margin:5px;
	display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    text-align: center;
}
@media(max-width:640px){
    .item{
        grid-template-columns: 1fr 1fr;
        grid-gap: 15px;
        gap: 15px;
    }
}
